[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    margin: 20px 0 10px 0;
    width: 171px;
    height: 36px;
    border: 1px solid #323232;
    border-radius: 2px;
}

.container {
    display: flex;
    flex-direction: column;
}

.text {
    font-family: 'Montserrat', Arial, serif;
    font-size: 14px;
    font-weight: normal;
}